import React from 'react';
import s from './styles.module.css'

const List = () => {
    return (
        <div className={s.list}>
            <div className={s.item1}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}> </p>
                </div>
                <p className={s.sub_title}>      15 </p>
            </div>
            <div className={s.item2}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}></p>
                </div>
                <p className={s.sub_title}>       </p>
            </div>
            <div className={s.item3}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}>-</p>
                </div>
                <p className={s.sub_title}>    </p>
            </div>
            <div className={s.item4}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}></p>
                </div>
                <p className={s.sub_title}>100+   ,   </p>
            </div>
        </div>
    );
};

export default List;
