import React from 'react';
import {Box, Modal, SwipeableDrawer, useMediaQuery} from "@mui/material";
import s from './styles.module.css'
import {ReactComponent as CloseIcon} from "../../../assets/svg/close.svg";

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    maxWidth: '1000px',
    width: '100%',
    background: '#2B2A37',
    borderRadius: '32px',
    boxShadow: 0,
    display: 'flex',
    flexDirection: 'column',
    padding: '48px',
    height: '582px',
    '@media (max-width: 780px)': {
        width: '100%',
        height: '100dvh',
        borderRadius: 0,
        padding: '32px 16px 16px 16px'
    }
};

const style2 = {
    // position: 'absolute',
    // bottom: '0px',
    width: '100%',
    background: '#2B2A37',
    boxShadow: 0,
    display: 'flex',
    flexDirection: 'column',
    borderRadius: '16px 16px 0px 0px',
    height: '95dvh',
    padding: '0 16px',
    alignItems: 'center',
    justifyContent: 'center',
    color: '#FFF',
    fontFamily: 'Inter,sans-serif',
    fontSize: '16px',
    fontStyle: ' normal',
    fontWeight: ' 400',
    lineHeight: '140%',
    paddingTop: '12px',
    paddingBottom: '10px'
};

const data = [
    {
        number: 1,
        title: '',
        subtitle: <p className={s.modal_title}>          
               .
                ,     .     
               ,        .
                   .         
            ,    </p>
    },
    {
        number: 2,
        title: '  ',
        subtitle: <p className={s.modal_title}>       ,
              
            Junior.  -   :  4   ,   2 
            ,  2 - .  ,      
                   -    
            ,      ,          .
                ,          (,
              - ,       ..),    , 
             ,    .</p>
    },
    {
        number: 3,
        title: ' ',
        subtitle: <p className={s.modal_title}>    ,  
              
              ,    (  !):       ,  
                            
            ,     ,         
                .</p>
    },
    {
        number: 4,
        title: '  ',
        subtitle: <>
            <p className={s.modal_title}>      
                   
                  :</p>
            <div className={s.ul}>
                <p>HoldemResources Calculator (HRC)</p>
                <p>CardRunners EV (CREV)</p>
                <p>Pokerstrategy Equilab</p>
                <p>FlopZilla</p>
                <p>Icmizer</p>
                <p>Gto+</p>
                <p>HoldEq</p>
                <p>SimplePreflopHoldem</p>
                <p>SimplePostflopHoldem</p>
                <p>Simple 3 way</p>
                <p>GTO Trainer</p>
                <p>GTO Base</p>
                <p>GTODB</p>
            </div>
            <p className={s.modal_title}>     !    ,   
                . ,
                    - PokerHub -        
                -,    ,         ,
                 ,      .</p>
        </>
    },
    {
        number: 5,
        title: '',
        subtitle: <p className={s.modal_title}> ,        
             ,
                       ,  , 
            ,         .      
               ,    .      ,  
             (,      , ,   . .)</p>
    },
]
const ModalSupport = ({handleClose, open, setOpen}) => {
    const matches = useMediaQuery('(max-width:700px)');

    const container = window !== undefined ? () => window.document.body : undefined;
    const toggleDrawer = (newOpen) => () => {
        setOpen(newOpen);
    };
    const iOS =
        typeof navigator !== 'undefined' && /iPad|iPhone|iPod/.test(navigator.userAgent);
    return matches ? <SwipeableDrawer
        disableBackdropTransition={!iOS} disableDiscovery={iOS}
        sx={{

            '& .MuiPaper-root': {
                background: 'rgba(0, 0, 0, 0.5);',
                borderRadius: '16px 16px 0px 0px',
                // opacity:'0'
            }
        }}
        container={container}
        anchor="bottom"
        open={open}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
        swipeAreaWidth={36}
        disableSwipeToOpen={true}
        ModalProps={{
            keepMounted: true,
        }}
    >

        <div className={s.line}/>

        <Box sx={style2}>
            <div className={s.header}>
                <div className={s.close} onClick={handleClose}>
                    <CloseIcon/>
                </div>
                <h1 className={s.title}>   </h1>
                <p className={s.sub_title}>  </p>
            </div>

            <div className={s.content}>
                <h1 className={s.title}>   </h1>
                <p className={s.sub_title}>  </p>
                {data.map((el) => <div className={s.item}>
                    <div className={s.item_top}>
                        <div className={s.number}>{el.number}</div>
                        <p className={s.item_title}>{el.title}</p>
                    </div>
                    <div className={s.item_bottom}>
                        {el.subtitle}
                    </div>
                </div>)}
            </div>
        </Box>
    </SwipeableDrawer> : <Modal
        open={open}
        onClose={handleClose}
    >
        <Box sx={style}>
            <div className={s.header}>
                <div className={s.close} onClick={handleClose}>
                    <CloseIcon/>
                </div>
                <h1 className={s.title}>   </h1>
                <p className={s.sub_title}>  </p>
            </div>

            <div className={s.content}>
                {data.map((el) => <div className={s.item}>
                    <div className={s.item_top}>
                        <div className={s.number}>{el.number}</div>
                        <p className={s.item_title}>{el.title}</p>
                    </div>
                    <div className={s.item_bottom}>
                        {el.subtitle}
                    </div>
                </div>)}
            </div>
        </Box>
    </Modal>

};

export default ModalSupport;
