import React, {useState} from 'react';
import s from './styles.module.css'
import Item from "./item";
import classNames from "classnames";

const data_1 = [
    {title: '   ', number: '1.1'},
    {title: ' ', number: '1.2'},
    {title: '   Spin&Go   ', number: '1.3'},
]

const data_2 = [
    {title: '  ', number: '2.1'},
    {title: '    Button (BU)  3-max', number: '2.2'},
    {title: '-   Small Blind (SB)  3-max.  ', number: '2.3'},
    {title: '    SB  3-max    (SBB)', number: '2.4'},
    {title: '    SB   3-max      Button', number: '2.5'},
]
const data_3 = [
    {title: '   ', number: '3.1'},
    {title: ' ()  ', number: '3.2'},
    {title: ' ()  ', number: '3.3'},
    {title: ' ( )  ', number: '3.4'},
    {title: ' ( )  ', number: '3.5'},
]
const Progrm = () => {
    const [showMore, setShowMore] = useState(false)

    const data_2_check = showMore ? [...data_2, ...data_2] : data_2
    return (
        <div className={s.main}>
            <div className={s.header}>
                <h1 className={s.header_title}> </h1>

                <div className={s.header_description}>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>2</div>
                        <p className={s.header_item_title}>  </p>
                    </div>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>6</div>
                        <p className={s.header_item_title}></p>
                    </div>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>25+</div>
                        <p className={s.header_item_title}></p>
                    </div>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>60+</div>
                        <p className={s.header_item_title}> </p>
                    </div>
                </div>
            </div>

            <div className={s.content}>
                <div className={s.box}>
                    <h1 className={s.box_title}> 1.   </h1>
                    <p className={classNames(s.box_sub_title, s.box_sub_title1)}>  ,     
                          ,  
                                SpinnGo.</p>

                    <div className={s.items}>
                        {data_1.map((el) => <Item {...el} key={el.number}/>)}
                    </div>
                </div>
                <div className={s.box}>
                    <h1 className={s.box_title}> 2.    </h1>
                    <p className={classNames(s.box_sub_title, s.box_sub_title2)}>    
                                .  
                           ,      
                          .</p>

                    <div className={s.items}>
                        {data_2_check.map((el, i) => <Item {...el} key={el.number + i}/>)}
                    </div>

                    <div className={s.show_more}
                         onClick={() => setShowMore(!showMore)}>{showMore ? '' : ' '}</div>
                </div>
                <div className={s.box}>
                    <h1 className={s.box_title}> 3.    </h1>
                    <p className={classNames(s.box_sub_title, s.box_sub_title3)}>   ()
                           .   ,      
                        .</p>

                    <div className={s.items}>
                        {data_3.map((el) => <Item {...el} key={el.number}/>)}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Progrm;
