import React from 'react';
import s from './styles.module.css'
import avatar from '../../../assets/img/avatar.png'
import Button from "../../../component/button";
import {ReactComponent as TgIcon} from "../../../assets/svg/tg.svg";

const Author = () => {
    return (
        <div className={s.main}>
            <div className={s.main_top}>
                <div className={s.main_top_left}>
                    <p className={s.desc}>   </p>
                    <p className={s.name}> </p>

                    <img src={avatar} className={s.avatar_m} alt="avatar"/>

                    <div className={s.ul}>
                        <p>    15 </p>
                        <p>     10  </p>
                        <p>      .  - Spin`n`Go</p>
                    </div>

                    <p className={s.sub_desc}>        ,  -
                            .         
                                 Beginner    
                          .</p>
                </div>
                <img src={avatar} className={s.avatar} alt="avatar"/>
            </div>

            <div className={s.main_bottom} id='tg_block'>
                <h2 className={s.title}>   <br/>  </h2>
                <p className={s.sub_title}>            3 .
                          ,   .  ,  ,  
                    </p>
                <a href="https://t.me/Academicoff" target={'_blank'} style={{width: '100%'}}>
                    <Button className={s.btn}>
                        <div className={s.btn_content}>
                            <TgIcon/>
                            <p> </p>
                        </div>
                    </Button>
                </a>
            </div>
        </div>
    );
};

export default Author;
