import React, {useState} from 'react';
import s from './styles.module.css'
import classNames from "classnames";
import {ReactComponent as Arrow} from "../../../assets/svg/arrow.svg";
import ModalSupport from "../../../component/modal/modal_support";

const Best = () => {
    const [open, setOpen] = useState(false)
    return (
        <div className={s.main}>
            <ModalSupport open={open} setOpen={setOpen} handleClose={() => setOpen(false)}/>
            <div className={s.main_left}>
                <h1 className={s.main_left_title}>    </h1>
                <p className={s.main_left_sub_title}>    ,     ,
                          </p>
            </div>
            <div className={s.main_right}>
                <div className={s.item}>
                    <h2 className={classNames(s.number, s.number_1)}>1</h2>
                    <h1 className={s.item_title}></h1>
                    <p className={s.item_sub_title}>    </p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>2</h2>
                    <h1 className={s.item_title}>  </h1>
                    <p className={s.item_sub_title}>  ,     
                          </p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>3</h2>
                    <h1 className={s.item_title}> </h1>
                    <p className={s.item_sub_title}>   ,     </p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>4</h2>
                    <h1 className={s.item_title}>  </h1>
                    <p className={s.item_sub_title}>      
                        </p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>5</h2>
                    <h1 className={s.item_title}></h1>
                    <p className={s.item_sub_title}>     ?     
                        </p>
                </div>
                <div className={s.last} onClick={() => setOpen(true)}>
                    <h1 className={s.last_item_title}>   </h1>
                    <div className={s.arrow}>
                        <Arrow/>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Best;
