import React from 'react';
import s from './styles.module.css'
// import img from '../../../assets/img/learn.jpg'
import img from '../../../assets/img/learn.png'
import Button from "../../../component/button";
import AnchorLink from "react-anchor-link-smooth-scroll/lib/anchor-link";

const StartLearn = () => {
    return (
        <div className={s.box}>
            <div className={s.left_box}>
                <div className={s.cheaps}>
                    <div className={s.cheap}></div>
                    <div className={s.cheap}> </div>
                    <div className={s.cheap}>  </div>
                </div>
                <h1 className={s.title}>
                      :
                    <br/>
                        
                </h1>

                <p className={s.sub_title}>       ,    
                         .</p>
                <AnchorLink
                    offset={30}
                    href={`#tg_block`}
                >
                    <Button className={s.btn}>
                         
                    </Button>
                </AnchorLink>
            </div>

            <img className={s.img} src={img} alt="cards"/>
        </div>
    );
};

export default StartLearn;
